<template>
    <div class="inner-wrap">
        <user-header v-show="isShowUser" :userInfoData="authorizeUserInfo"></user-header>
        <router-view @getUserInfo="getUserInfo" />
        <van-popup :close-on-click-overlay="false" v-model="load_user_msg">
            <p class="showTip">{{load_user_info}}</p>
        </van-popup>

        <van-popup :close-on-click-overlay="false" v-model="authrized_failed">
            <p class="showTip">由于授权失败，您的账户存在安全问题(首次出现提示,请刷新当前页，或关闭页面重新进入,给您带来的不变，敬请谅解)，将暂时无法进行任何操作!请联系我司客服，我们将尽快为您解决问题。</p>
        </van-popup>

    </div>
</template>

<script>

    import {Notify} from 'vant'
    import {mapState} from 'vuex'
    import userHeader from './common/uesrHead'
    import {Dialog, Popup} from 'vant'
    import {_post, _get} from "../http";
    import {codeParam, checkBrowser, setStorage, getStorage, removeStorage, getUrlParam, checkICCID,GetUrlRelativePath} from "../utilies";
    export default {
        name: 'App',
        data() {
            return {
                client_type: checkBrowser(),//微信/支付宝环境
                state: '',//防跨域攻击
                appContext: false,//是否app环境
                load_user_msg: false,
                load_user_info:'加载用户信息,请等候',
                authrized_failed:false,//授权失败信息
            }
        },
        components: {
            userHeader,
            [Dialog.name]: Dialog,
            [Notify.name]: Notify,
            [Popup.name]: Popup,
        },
        computed: {
            ...mapState({
                isShowUser: state => state.userInfo.showUser,
                authorizeUserInfo: state => state.userInfo.userInfoInner
            }),
        },
        created() {
            //手表扫码跳转
            if (getUrlParam('iccid')) {
                let watch_iccid = getUrlParam('iccid');
                if (checkICCID(watch_iccid).state == 1) {
                    setStorage('watch_card', watch_iccid)
                    setStorage('watchAutoSearch', 1);
                }
            } else {
                removeStorage('watch_card');
                removeStorage('watchAutoSearch');
            }

            if (this.client_type == 'app') {
                document.addEventListener("plusready", this.plusReady, false);
            } else {
                this.authorized()
            }

        },
        methods: {
            plusReady() {
                localStorage.setItem("token", plus.storage.getItem("appToken"));
                this.authorized()
            },

            authorized() {
                if (this.client_type == 'wechat' || this.client_type == 'alipay' || this.client_type == 'app') {

                    if (this.client_type != 'app') {

                        if (getStorage('userInfo', 'obj')) {

                            this.$store.commit('userInfo/changeUserStatus', true);
                        }

                        if ((this.client_type == 'wechat' && getStorage('wechat_version') != this.global_variables.version) ||

                            (this.client_type == 'alipay' && getStorage('alipay_version') != this.global_variables.version)
                        ) {
                            removeStorage('token');
                            removeStorage('auth_data');
                            removeStorage('state');
                        }

                    }//app环境隐藏顶部个人信息

                    if (getStorage('token')) {
                        this.getUserInfo();//获取用户信息
                    } else {

                        if (getUrlParam('data')) {
                            setStorage('auth_data', getUrlParam('data'))
                        }

                        if (getStorage('auth_data')) {
                            /*
                            * 已授权操作 重定向后操作
                            * */

                            // if (getStorage('state') == getUrlParam('state')) { // 放开校验

                                // 新增解密数据遮罩 避免用户操作过快
                                this.load_user_msg = true;
                                this.load_user_msg = '获取用户信息,请等候'
                                //解密data
                                _post('/accountCenter/v2/secret/decrypt?' + codeParam({}, 'post'), {
                                    data: getStorage('auth_data')
                                }).then(res => {

                                    if (res.error == 0) {

                                        if(res.data.data && JSON.stringify(res.data.data)!='{}'){//确保用户中心存在返回数据

                                            if (this.client_type == 'alipay') {

                                                let aliUser = {
                                                    uuid: res.data.data.user_id,
                                                    openid: res.data.data.user_id,
                                                    headimgurl: res.data.data.avatar,
                                                    gender: res.data.data.gender == 'm' ? 1 : 2,
                                                    nickname: res.data.data.nick_name
                                                }

                                                setStorage('decrypt_data', aliUser, 'obj');


                                            } else if (this.client_type == 'wechat') {

                                                let wechatUserData = res.data.data;
                                                if (wechatUserData.unionid) {
                                                    wechatUserData.openid = wechatUserData.unionid
                                                }
                                                setStorage('decrypt_data', wechatUserData, 'obj');
                                            }

                                            //login
                                            this.userLogin(res.data.code);

                                        }else{
                                            this.load_user_msg = '您的账户信息异常,请截图并发送您的手机号码给我司客服。我们将尽快为您解决问题，感谢您的支持与配合(sd)。'

                                        }

                                    } else if (res.error == '11002') {

                                        this.$emit('getToken')

                                    } else {

                                        res.msg ? this.load_user_msg = res.msg : this.load_user_msg ='您的账户信息异常,请截图并发送您的手机号码给我司客服。我们将尽快为您解决问题，感谢您的支持与配合(sd-1)。'
                                    }
                                })
                                // end 状态
                            // } else {
                            //     let _this =this;
                            //     removeStorage('auth_data');
                            //     removeStorage('token');
                            //     removeStorage('state');
                            //
                            //     Dialog.alert({
                            //         title: '授权失败',
                            //         message: '您的账号暂时无法使用，充值查询将受到影响(首次出现提示,请刷新当前页，或关闭页面重新进入,给您带来的不变，敬请谅解)。请与我司客服联系，我们将尽快为您解决。',
                            //     }).then(() => {
                            //         _this.authrized_failed = true;
                            //     })
                            //
                            // }
                            /*
                           * end 已授权操作
                           * */
                        } else {
                            let _this = this;
                            Dialog.alert({
                                title: '授权',
                                message: '为了您的账号安全，我们需要您对本站进行授权操作并绑定账号。'
                            }).then(() => {
                                _this.authorizedRediect()
                            })
                        }
                    }

                } else {
                    this.$store.commit('userInfo/changeUserStatus', false);
                }
            },

            getUserInfo() {
                //获取用户信息
                this.load_user_msg = true;//用户信息遮罩
                _get("/accountCenter/v2/user/info?" + codeParam({}, 'get'))
                    .then(res => {
                        if (res.error == 0) {

                            if (res.data && JSON.stringify(res.data) != '{}') {
                                let UserInfo = {
                                    account: res.data.account,
                                    avatar: res.data.avatar,
                                    nickname: res.data.nickname,
                                }

                                setStorage('userInfo', UserInfo, 'obj');

                                if (this.client_type == 'wechat' || this.client_type == 'alipay') {
                                    this.$store.commit('userInfo/changeUserStatus', true);
                                    this.$store.commit('userInfo/changeUserInfo', UserInfo);
                                }

                                this.load_user_msg = false;

                            } else {

                                let _this = this;

                                Dialog.alert({
                                    title: '账号异常',
                                    message: '您的账户信息存在问题，请截图并发送您的手机号码给我司客服。我们将尽快为您解决问题，感谢您的支持与配合(ui)。',
                                }).then(() => {
                                    _this.load_user_msg = true;
                                    _this.load_user_info  = '账号异常';

                                })

                            }
                        } else if (res.error == "11002") {

                            this.load_user_msg = '为您更新用户口令,请等候'

                            this.$emit('getToken')

                        } else {

                            res.msg ? this.load_user_msg = res.msg : this.load_user_msg ='您的账户信息异常,请截图并发送您的手机号码给我司客服。我们将尽快为您解决问题，感谢您的支持与配合(ui-1)'
                        }
                    })

            },// 获取用户信息

            showDoc() {
                Dialog.alert({
                    message: '钻石：翼联会员体系下通用虚拟货币,可以用于:购买套餐,充值话费,游戏娱乐,购买优惠商品;\nELB：可通过阅读微信文章、充值话费和活动套餐等方式免费领取，用于商品现金抵扣、游戏娱乐等'
                })
            },
            authorizedRediect() {
                this.state = Math.random().toString(36).substr(2);
                setStorage('state', this.state);

                //获取当前重定向地址
                let redirect_uri = GetUrlRelativePath();
                /\/weixin\/[a-zA-Z]/.test(redirect_uri) ? setStorage('authorized_redirect_uri', redirect_uri) : setStorage('authorized_redirect_uri', '/weixin/new_auth');

                //存储手表二维码ICCID
                if (getUrlParam('iccid')) {
                    let watch_iccid = getUrlParam('iccid');
                    if (checkICCID(watch_iccid).state == 1) {
                        setStorage('watch_card', watch_iccid)
                        setStorage('watchAutoSearch', 1);
                    }
                } else {
                    removeStorage('watch_card');
                    removeStorage('watchAutoSearch');
                }

                // 授权
                _get('/accountCenter/v2/oauth/authorize?' + codeParam({
                    client_type: this.client_type,
                    redirect_uri: this.global_variables.authorized_redirect_url + redirect_uri,
                    scope: 'userinfo',
                    state: this.state
                }, 'get'))
                    .then(res => {
                        if (res.error == 0) {

                            if (this.client_type == 'wechat') {
                                setStorage('wechat_version', this.global_variables.version);
                            } else if (this.client_type == 'alipay') {
                                setStorage('alipay_version', this.global_variables.version);
                            }

                            location.href = res.data;

                        } else if (res.error == '11002') {

                            this.$emit('getToken')

                        } else {

                            Notify({
                                message: res.msg
                            })
                        }
                    })
            },//授权操作
            userLogin(code){
                _post('/accountCenter/v2/auth/login?' + codeParam({}, 'post'), {
                    uuid: getStorage('decrypt_data', 'obj').openid,
                    // code: res.data.code
                    code: code
                }).then(res => {

                    if (res.error == 0) {

                        if(res.data){
                            setStorage('token', res.data);//获取token
                            this.getUserInfo();//获取用户信息
                        }else{
                            this.load_user_msg ='您的账号异常,退出本页面重新访问';
                        }

                    } else if (res.error == '11002') {

                        this.$emit('getToken')

                    } else if (res.error == '30005' || res.error == '11003') {

                        let _this = this;

                        Notify({message: '为了您的账号安全,请绑定手机号码'});

                        setTimeout(function () {
                            _this.$router.push({path: '/login'})
                        }, 2000)

                    } else {
                        res.msg ? this.load_user_msg = res.msg : this.load_user_msg ='您的账户信息异常,请截图并发送您的手机号码给我司客服。我们将尽快为您解决问题，感谢您的支持与配合(lg-1)'

                    }
                })

            },//获取用户token并登录

        }
    }
</script>


